___CSS_LOADER_EXPORT___.push([module.id, `.drop_down {
  background-color: rgb(60, 60, 184);
  position: fixed;
  width: 100%;
  transition: opacity ease-in-out 300ms;
  opacity: 1;
  z-index: 10;
}

.drop_down ul {
  align-self: center;
  list-style: none;
  margin: 0 0 1rem 0;
  color: white;
  width: 100%;
  height: 100%;
  top: 1rem;
  padding: 1rem;
  text-align: center;
}

.drop_down ul li {
  position: relative;
  margin: 1rem 0;
  padding: 1rem;
}

.drop_down ul li:hover {
  background-color: blueviolet;
}
